@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
.tab {
  padding: 0;
  text-align: center;
  list-style: none;
  display: inline-flex;
  border: 2px solid #011936;
  border-radius: 5px;
  margin: 10px 5px 0px 5px;
  height: 30px;
}

.tab li {
  width: 100px;
  padding: 5px 10px;
  background: white;
  display: inline-block;
  font-size: 18px;
}

.tab li:hover{
  background: #a6d8d4;
  font-weight: bold;
  cursor: default;
}

.middleList {
  border-right: 2px solid #011936;
  border-left: 2px solid #011936;
}


#active{
  color: white;
  background: #011936;
  font-weight: bold;
}

.button {
  padding: 15px;
  margin-top: 60px;
  border: 1px solid #011936;
  border-radius: 10px;
  font-size: 24px;
  background-color: #011936;
  color: white;
  outline: none;
  text-decoration: none;
}

.buttonEnded{
  padding: 15px;
  margin-top: 20px;
  border: 1px solid #5a0002;
  border-radius: 10px;
  font-size: 24px;
  background-color: #5a0002;
  color: white;
  outline: none;
}

.button:hover {
  padding: 15px;
  margin-top: 60px;
  border: 1px solid #465362;
  border-radius: 10px;
  background-color: #465362;
  color: white;
  outline: none;
}

.wrapper {
  text-align: center;
  width: 100%;
}


.pEnded {
  font-family: Roboto;
  font-weight: lighter;
}

.foodType {
  display:flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  font-family: Roboto;
  margin-top: 10px;
}

.foodWrapper {
  height: 300px;
  width: 380px;
  border-radius: 5px;
}


.foodName {
  font-size: 24px;
  font-weight: bold;
  margin: 60px 0px 20px 0px;
}

.background{
  background-image: url(/randomsgfood/static/media/background2.2eceeef0.jpg);
  width: 100%;
  height: 32%;
  background-repeat: no-repeat;
  background-size: cover;
}
.header {
  display:flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 32%;
  background-color: rgb(255,255,255,0.6);
  margin-top: 5px;
}

.heading{
  width: 100%;
  color: black;
  font-size: 4vw;
  font-weight: bold;
  font-family: 'Merriweather', serif;
}









@media only screen and (max-width: 600px) {
  .foodWrapper {
    height: 200px;
    width: 240px;
    border-radius: 5px
  }

  .foodName {
    margin: 20px 0px 0px 0px;
  }

  .heading{
    width: 100%;
    font-size: 20px;
  }
  .foodType {
    margin: 0px;
  }

  .header{
    margin-top: 0px;
  }

  .button {
    margin-top:50px;
    font-size: 18px;
    padding: 10px;
  }

  .buttonEnded {
    margin-top: 50px;
    font-size: 18px;
    padding: 10px;
  }

  .button:hover {
    margin-top:50px;
    font-size: 18px;
    padding: 10px;
  }

  .foodName {
    display: flex;
    align-items: center;
    font-size: 18px;
    height: 50px;
  }

  .tab {
    margin-top: 10px;
  }

  .tab li{
    font-size: 15px;
    padding: 5px 3px;
    width: 80px;
  }
}

